"use client";

import { useState, useEffect } from "react";
import { observer } from "mobx-react-lite";
import { productStore } from "../stores/ProductStore"; // Импортируем store
import { Product } from "../types/types";
import { Input } from "./ui/input";
import { Button } from "./ui/button";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "./ui/table";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import { ProductModalComponent } from "./product-modal";
import { organizationStore } from "../stores/OrganizationStore";
import { personStore } from "../stores/PersonStore";
import { toJS } from "mobx";
import { addressStore } from "../stores/AddressStore";
import { locationStore } from "../stores/LocationStore";

export const ProductList = observer(() => {
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(10);
  const [sortField, setSortField] = useState<keyof Product>("id");
  const [sortDirection, setSortDirection] = useState<"asc" | "desc">("asc");
  const [filterName, setFilterName] = useState("");

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);

  // Функция для сортировки продуктов
  const sortProducts = (field: keyof Product) => {
    if (field === sortField) {
      setSortDirection(sortDirection === "asc" ? "desc" : "asc");
    } else {
      setSortField(field);
      setSortDirection("asc");
    }
  };

  // Функция для фильтрации продуктов
  const filterProducts = () => {
    return productStore.products.filter((product) =>
      product.name.toLowerCase().includes(filterName.toLowerCase())
    );
  };

  // Получение текущей страницы продуктов
  const getCurrentProducts = () => {
    const filteredProducts = filterProducts();
    const sortedProducts = [...filteredProducts].sort((a, b) => {
      const aField = a[sortField] ?? ""; // Устанавливаем значение по умолчанию, если `null` или `undefined`
      const bField = b[sortField] ?? "";
      if (aField < bField) return sortDirection === "asc" ? -1 : 1;
      if (aField > bField) return sortDirection === "asc" ? 1 : -1;
      return 0;
    });
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    return sortedProducts.slice(indexOfFirstItem, indexOfLastItem);
  };

  // Изменение страницы
  const paginate = (pageNumber: number) => setCurrentPage(pageNumber);

  // Открыть модалку для создания нового продукта
  const handleCreateProduct = () => {
    setSelectedProduct(null); // Устанавливаем `null` для режима создания
    setIsModalOpen(true);
  };

  const handleEditProduct = (product: Product) => {
    setSelectedProduct(product); // Передаем продукт для редактирования
    setIsModalOpen(true);
  };

  // Сохранить продукт
  const handleSaveProduct = async (product: Partial<Product>) => {
    if (selectedProduct) {
      await productStore.updateProduct(selectedProduct.id, product as Product);
    } else {
      await productStore.createProduct(product as Product);
    }
    setIsModalOpen(false);
  };

  return (
    <>
      <Card className='w-full'>
        <CardHeader>
          <CardTitle>Список продуктов</CardTitle>
        </CardHeader>
        <CardContent>
          <div className='flex justify-between mb-4'>
            <Input
              placeholder='Фильтр по названию'
              value={filterName}
              onChange={(e) => setFilterName(e.target.value)}
              className='max-w-sm'
            />
            <Button onClick={handleCreateProduct}>Создать продукт</Button>
          </div>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead onClick={() => sortProducts("id")}>ID</TableHead>
                <TableHead onClick={() => sortProducts("name")}>
                  Название
                </TableHead>
                <TableHead>Координаты</TableHead>
                <TableHead onClick={() => sortProducts("creationDate")}>
                  Дата создания
                </TableHead>
                <TableHead>Единица измерения</TableHead>
                <TableHead>Производитель</TableHead>
                <TableHead onClick={() => sortProducts("price")}>
                  Цена
                </TableHead>
                <TableHead>Стоимость производства</TableHead>
                <TableHead onClick={() => sortProducts("rating")}>
                  Рейтинг
                </TableHead>
                <TableHead>Номер партии</TableHead>
                <TableHead>Владелец</TableHead>
                <TableHead>Действия</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {getCurrentProducts().map((product) => (
                <TableRow key={product.id}>
                  <TableCell>{product.id}</TableCell>
                  <TableCell>{product.name}</TableCell>
                  <TableCell>{`(${product.coordinates.x}, ${product.coordinates.y})`}</TableCell>
                  <TableCell>
                    {product.creationDate
                      ? new Date(product.creationDate).toUTCString()
                      : "N/A"}
                  </TableCell>
                  <TableCell>{product.unitOfMeasure}</TableCell>
                  <TableCell>{product.manufacturer.name}</TableCell>
                  <TableCell>{product.price}</TableCell>
                  <TableCell>{product.manufactureCost ?? "N/A"}</TableCell>
                  <TableCell>{product.rating}</TableCell>
                  <TableCell>{product.partNumber}</TableCell>
                  <TableCell>{product.owner.name}</TableCell>
                  <TableCell>
                    <Button
                      variant='outline'
                      onClick={() => handleEditProduct(product)}
                      className='mr-2 mb-2'
                    >
                      Редактировать
                    </Button>
                    <Button
                      variant='destructive'
                      onClick={() => productStore.deleteProduct(product.id)}
                    >
                      Удалить
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
          <div className='flex justify-center mt-4'>
            {Array.from(
              {
                length: Math.ceil(productStore.products.length / itemsPerPage),
              },
              (_, i) => (
                <Button
                  key={i}
                  onClick={() => paginate(i + 1)}
                  variant={currentPage === i + 1 ? "default" : "outline"}
                  className='mx-1'
                >
                  {i + 1}
                </Button>
              )
            )}
          </div>
        </CardContent>
      </Card>

      <ProductModalComponent
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onSave={handleSaveProduct}
        product={toJS(selectedProduct)}
      />
    </>
  );
});
