"use client";

import { useEffect, useState } from "react";
import { observer } from "mobx-react-lite";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "./ui/table";
import { Button } from "./ui/button";
import { personStore } from "../stores/PersonStore";
import { locationStore } from "../stores/LocationStore";
import { Person, Color, Country } from "../types/types";

import CreatePersonModal from "./CreatePersonModal";
import EditPersonModal from "./EditPersonModal";
import { toJS } from "mobx";

const initialPerson: Person = {
  id: 0,
  name: "",
  birthday: undefined,
  hairColor: Color.BROWN,
  eyeColor: Color.RED,
  nationality: Country.RUSSIA,
  location: { id: 0, x: 0, y: 0, z: 0 },
};

const PersonTableComponent = observer(() => {
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [currentPerson, setCurrentPerson] = useState<Person>(initialPerson);

  useEffect(() => {
    personStore.loadPersons();
    locationStore.loadLocations();
  }, []);

  const handleEdit = (person: Person) => {
    const plainPerson = toJS(person); // Преобразуем MobX-прокси в обычный объект
    setCurrentPerson(plainPerson);
    setIsEditModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    const isConfirmed = window.confirm(
      "Вы уверены, что хотите удалить этого владельца?"
    );
    if (isConfirmed) {
      await personStore.deletePerson(id);
    }
  };

  return (
    <div className='container mx-auto p-4'>
      <Button
        onClick={() => {
          setCurrentPerson(initialPerson);
          setIsCreateModalOpen(true);
        }}
        className='mb-4'
      >
        Создать владельца
      </Button>
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>ID</TableHead>
            <TableHead>Имя</TableHead>
            <TableHead>Дата рождения</TableHead>
            <TableHead>Цвет глаз</TableHead>
            <TableHead>Цвет волос</TableHead>
            <TableHead>Национальность</TableHead>
            <TableHead>Местоположение</TableHead>
            <TableHead>Действия</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {personStore.persons.map((person: Person) => (
            <TableRow key={person.id}>
              <TableCell>{person.id}</TableCell>
              <TableCell>{person.name}</TableCell>
              <TableCell>
                {person.birthday
                  ? new Date(person.birthday).toLocaleDateString("en-GB")
                  : "N/A"}
              </TableCell>
              <TableCell>{person.eyeColor || "N/A"}</TableCell>
              <TableCell>{person.hairColor}</TableCell>
              <TableCell>{person.nationality}</TableCell>
              <TableCell>
                {`(${person.location?.x}, ${person.location?.y}, ${person.location?.z})`}
              </TableCell>
              <TableCell>
                <Button
                  variant='outline'
                  size='sm'
                  onClick={() => handleEdit(person)}
                  className='mr-2'
                >
                  Редактировать
                </Button>
                <Button
                  variant='destructive'
                  size='sm'
                  onClick={() => handleDelete(person.id)}
                >
                  Удалить
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <CreatePersonModal
        isOpen={isCreateModalOpen}
        onClose={() => setIsCreateModalOpen(false)}
      />
      <EditPersonModal
        isOpen={isEditModalOpen}
        onClose={() => setIsEditModalOpen(false)}
        person={currentPerson}
      />
    </div>
  );
});

export default PersonTableComponent;
