"use client";

import { useState, useEffect } from "react";
import { observer } from "mobx-react-lite";
import { Button } from "./ui/button";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "./ui/table";
import { organizationStore } from "../stores/OrganizationStore";
import { Organization } from "../types/types";
import CreateOrganizationModal from "./CreateOrganizationModal";
import EditOrganizationModal from "./EditOrganizationModal";

const OrganizationTable = observer(() => {
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [currentOrganization, setCurrentOrganization] =
    useState<Organization | null>(null);

  useEffect(() => {
    organizationStore.loadOrganizations();
  }, []);

  const handleCreate = () => {
    setCurrentOrganization(null);
    setIsCreateModalOpen(true);
  };

  const handleEdit = (organization: Organization) => {
    setCurrentOrganization(organization);
    setIsEditModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    if (window.confirm("Вы уверены, что хотите удалить эту организацию?")) {
      await organizationStore.deleteOrganization(id);
    }
  };

  return (
    <div className='container mx-auto p-4'>
      <Button
        onClick={handleCreate}
        className='mb-4'
      >
        Создать организацию
      </Button>
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>ID</TableHead>
            <TableHead>Название</TableHead>
            <TableHead>Полное название</TableHead>
            <TableHead>Официальный адрес</TableHead>
            <TableHead>Почтовый адрес</TableHead>
            <TableHead>Годовой оборот</TableHead>
            <TableHead>Количество сотрудников</TableHead>
            <TableHead>Рейтинг</TableHead>
            <TableHead>Действия</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {organizationStore.organizations.map((org) => (
            <TableRow key={org.id}>
              <TableCell>{org.id}</TableCell>
              <TableCell>{org.name}</TableCell>
              <TableCell>{org.fullName}</TableCell>
              <TableCell>
                {org.officialAddress
                  ? `(${org.officialAddress.town?.x}, ${org.officialAddress.town?.y}, ${org.officialAddress.town?.z})\nZIP: ${org.officialAddress.zipCode}`
                  : "N/A"}
              </TableCell>
              <TableCell>
                {org.postalAddress
                  ? `(${org.postalAddress.town?.x}, ${org.postalAddress.town?.y}, ${org.postalAddress.town?.z})\nZIP: ${org.postalAddress.zipCode}`
                  : "N/A"}
              </TableCell>
              <TableCell>{org.annualTurnover || "N/A"}</TableCell>
              <TableCell>{org.employeesCount}</TableCell>
              <TableCell>{org.rating || "N/A"}</TableCell>
              <TableCell>
                <Button
                  variant='outline'
                  size='sm'
                  onClick={() => handleEdit(org)}
                  className='mr-2'
                >
                  Редактировать
                </Button>
                <Button
                  variant='destructive'
                  size='sm'
                  onClick={() => handleDelete(org.id)}
                >
                  Удалить
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <CreateOrganizationModal
        isOpen={isCreateModalOpen}
        onClose={() => setIsCreateModalOpen(false)}
      />
      <EditOrganizationModal
        isOpen={isEditModalOpen}
        onClose={() => setIsEditModalOpen(false)}
        organization={currentOrganization}
      />
    </div>
  );
});

export default OrganizationTable;
