"use client";

import { useEffect, useState } from "react";
import { observer } from "mobx-react-lite";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "./ui/table";
import { Button } from "./ui/button";
import { addressStore } from "../stores/AddressStore";
import { locationStore } from "../stores/LocationStore";
import { Address } from "../types/types";

import CreateAddressModal from "./CreateAddressModal";
import EditAddressModal from "./EditAddressModal";

const initialAddress: Address = {
  id: 0,
  zipCode: "505050",
  town: {
    x: 0,
    y: 0,
    z: 0,
    id: 0,
  },
};

const AddressTableComponent = observer(() => {
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [currentAddress, setCurrentAddress] = useState<Address | null>(null);

  useEffect(() => {
    addressStore.loadAddresses();
    locationStore.loadLocations();
  }, []);

  const handleEdit = (address: Address) => {
    setCurrentAddress(address);
    setIsEditModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    const isConfirmed = window.confirm(
      "Вы уверены, что хотите удалить этот адрес?"
    );
    if (isConfirmed) {
      await addressStore.deleteAddress(id);
    }
  };

  return (
    <div className='container mx-auto p-4'>
      <Button
        onClick={() => {
          setCurrentAddress(null);
          setIsCreateModalOpen(true);
        }}
        className='mb-4'
      >
        Создать адрес
      </Button>
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>ID</TableHead>
            <TableHead>Почтовый индекс</TableHead>
            <TableHead>Локация (X, Y, Z)</TableHead>
            <TableHead>Действия</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {addressStore.addresses.map((address) => (
            <TableRow key={address.id}>
              <TableCell>{address.id}</TableCell>
              <TableCell>{address.zipCode || "N/A"}</TableCell>
              <TableCell>
                {address.town
                  ? `(${address.town.x}, ${address.town.y}, ${address.town.z})`
                  : "N/A"}
              </TableCell>
              <TableCell>
                <Button
                  variant='outline'
                  size='sm'
                  onClick={() => {
                    handleEdit(address);
                  }}
                  className='mr-2 mb-2'
                >
                  Редактировать
                </Button>
                <Button
                  variant='destructive'
                  size='sm'
                  onClick={() => handleDelete(address.id)}
                >
                  Удалить
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>

      <CreateAddressModal
        isOpen={isCreateModalOpen}
        onClose={() => setIsCreateModalOpen(false)}
      />
      <EditAddressModal
        isOpen={isEditModalOpen}
        onClose={() => setIsEditModalOpen(false)}
        address={currentAddress || initialAddress}
      />
    </div>
  );
});

export default AddressTableComponent;
